export default {
	data() {
		return {
			amount: 0,
			orderName: '',
			ordelist: '', //支付订单
			paytype: 'alipay' //支付类型
		};
	},
	onLoad(e) {
		console.log(e);
		console.log(e.amount);
		this.amount = e.amount;

		// uni.getStorage({
		// 	key: 'payment_order',
		// 	success: e => {
		// 		this.ordelist = e.data;

		// 		// if (e.data.length > 1) {
		// 		// 	this.orderName = '多商品合并支付';
		// 		// } else {
		// 		// 	this.orderName = e.data[0].name;
		// 		// }
		// 		uni.removeStorage({
		// 			key: 'payment_order'
		// 		});
		// 	}
		// });
		uni.getStorage({
			key: 'payment_order',
			success: e => {
				if (e.data.length > 1) {
				} else {
					this.orderName = e.data[0].name;
				}
			}
		});
	},
	methods: {
		doDeposit() {
			let userId = '';
			uni.getStorage({
				key: 'userId',
				success: e => {
					userId = e.data;
				}
			});
			console.log(userId);
			
			let ids = '';
			uni.getStorage({
				key: 'order_ids',
				success: e => {
					ids = e.data;
				}
			});
			console.log(ids);
			
			if (this.paytype == 'yepay') {  //余额支付
				
				uni.navigateTo({
					url: '../../pay/payment/paymentPassword?amount=' + this.amount
				});
			} else {		//其他支付
				uni.showLoading({
					title: '支付中...'
				});
				let that = this;
				setTimeout(() => {
					uni.request({
						url: 'http://121.41.93.162:8080/travel-0.0.1-SNAPSHOT/pay/order/by/other',
						method: 'PUT',
						data: {
							"ids": ids,
							"totalMoney": that.amount,
							"userId": userId
						},
						success: res => {
							console.log(res);
						},
						fail: () => {},
						complete: () => {}
					});
				}, 500);
				uni.navigateTo({
					url: '../success/success?amount=' + this.amount
				});
			}
		}
	}
};
